import {
  AppBar,
  Typography,
  Link,
  Box,
  Toolbar,
  List,
  ListItem,
  ListItemText,
  IconButton,
  Drawer,
} from '@mui/material';
import React from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import useStyles from './styles/styles';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';
import data from "../data/data.json"
import './css/Navbar.css'

const Navbar = () => {
  const classes = useStyles();
  const links = [
    { id: 1, route: 'Home', url: '#' },
    { id: 2, route: 'About', url: '#about' },
    { id: 3, route: 'Owner', url: '#owner' },
    { id: 4, route: 'Contact', url: '#contact' },
  ];

  const [state, setState] = React.useState({
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <Box
      sx={{ width: 250 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {links.map((link) => (
          <ListItem button key={link.id}>
            <ListItemText primary={link.route} />
          </ListItem>
        ))}
      </List>
    </Box>
  );

  const theme = useTheme();
  const matches = useMediaQuery(theme.breakpoints.down('sm'));

  return (
    <Box sx={{ marginBottom: '70px' }} className='navbar navbar-default navbar-fixed-top container'>
        <AppBar className="container">
          <Toolbar className={classes.toolBar}>
            <Link href="#" underline="none" className="navbar-brand page-scroll">
              <Typography variant="h5">
                {data.Navigation.title}
              </Typography>
            </Link>

            {matches ? (
              <Box>
              <IconButton
                size="large"
                edge="end"
                color="inherit"
                aria-label="menu"
                onClick={toggleDrawer('right', true)}
              >
                <MenuIcon className={classes.menuIcon} fontSize="" />
              </IconButton>

              <Drawer
                anchor="right"
                open={state['right']}
                onClose={toggleDrawer('right', false)}
              >
                {list('right')}
              </Drawer>
            </Box>
            ): <Box
              sx={{
                display: 'flex',
                justifyContent: 'space-between',
                flexGrow: '0.1',
              }}
              className="nav navbar-nav navbar-right"
            >
              {links.map((link) => (
                <Link href={link.url} target="_blank" underline="none" key={link.id} className="page-scroll">
                  <Typography className={classes.link}>{link.route}</Typography>
                </Link>
              ))}
            </Box>}
           
          </Toolbar>
        </AppBar>
    </Box>
  );
};

export default Navbar;